import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'

import waimai from '../components/menu1/waimai.vue';
import order from '../components/menu2/order.vue';
import find from '../components/menu3/find.vue';
import mine from '../components/menu4/mine.vue';


Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'waimai',
            component: waimai
        }, {
            path: '/wm',
            name: 'waimai',
            component: waimai,
        }, {
            path: '/order',
            name: 'order',
            component: order,
        }, {
            path: '/find',
            name: 'find',
            component: find,
        }, {
            path: '/mine',
            name: 'mine',
            component: mine,
        }
    ]
})



