<template>
  <div id="app">
    <!--<img src="./assets/logo.png">-->
    <transition mode="out-in" :duration="{enter:100,leave:100}" enter-active-class="animated fadeInLeft" leave-active-class="animated fadeOutLeft">
        <router-view style='position: absolute;flex:auto;'></router-view>
    </transition>
    <tap-menu style="flex:none;"></tap-menu>
  </div>
</template>

<script>
  import tapMenu from './components/TapMenu.vue';
  export default {
    name: 'app',
    components:{
        tapMenu
    }
  }
</script>

<style>

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  display: flex;
  flex-direction:column;
  height:100%;

}
*{
  box-sizing: border-box;
}
[data-dpr = "1"] * {
  font-size: 12px!important;
}

[data-dpr = "2"] * {
  font-size: 24px!important;;
}

[data-dpr = "3"] * {
  font-size: 36px!important;;
}
</style>



